@keyframes header_top {
  0% {
    top: -80px;
  }
  100% {
    top: 0;
  }
}
.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 80px;
  @include z-index(header);
  transition: background-color .5s;
  color: $color_white;
  @include mq-max(md) {
    height: auto;
  }
  &-inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include section-layer;
    padding: 20px;
  }
  body.is-fixed & {
    position: fixed;
    background-color: rgba(255,255,255, .9);
    animation: header_top .5s forwards;
  }
}
.logo {
  margin-right: 120px;
  @include mq-max(sm) {
    margin-right: 0;
  }
  &_kadan {
    @include mq-max(sm) {
      width: 65%;
    }
  }
}
.nav-global {
  margin-right: auto;
  font-weight: bold;
  @include mq-max(sm) {
    display: none;
  }
  &__list {
    display: flex;
    align-items: center;
  }
  &__item {
    padding-right: 30px;
    &:last-of-type {
      padding-right: 0;
    }
  }
  body.is-fixed & {
    color: $color_main;
  }
}
.rv__btn {
  position: absolute;
  top: 13px;
  right: 20px;
  width: 170px;
  background-color: $bg-color_reservation;
  font-weight: bold;
  @include mq-max(sm) {
    display: none;
    width: 150px;
  }
  &-inner {
    display: flex;
    justify-content: center;
    align-items: center;  
  }
  &-text {
    padding: 19px 0 19px 10px;
    @include mq-max(sm) {
      padding: 15px 0 15px 10px;
    }
  }
}
.hb__btn {
  display: none;
  position: fixed;
  top: 12px;
  right: 0;
  padding: 20px;
  @include mq-max(sm) {
    display: block;   
  }
  @include z-index(hb__btn);
  &_line {
    position: relative;
    width: 24px;
    height: 2px;
    background-color: $color_white;
    border-radius: 5px;
    transition: all .3s ease-in-out;
    &::before,
    &::after {
      content: '';
      position: absolute;
      display: block;
      width: 24px;
      height: 2px;
      background-color: $color_white;
      border-radius: 5px;
      transition: all .3s ease-in-out;
    }
    &::before {
      top: -8px;
    }
    &::after {
      bottom: -8px;
    }
    .page & {
      background-color: $color_main;
      &::before,
      &::after {
        background-color: $color_main;
      }
    }
  }
}
body.is-fixed .hb__btn_line {
  background-color: $color_main;
  &::before,
  &::after {
    background-color: $color_main;
  }
}
body.hb_is-open .hb__btn_line {
  background-color: transparent;
  &::before {
    transform: rotate(-45deg);
    top: 0;
    background-color: $color_main;
  }
  &::after {
    transform: rotate(45deg);
    bottom: 0;
    background-color: $color_main;
  }
}
.hb__bg {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0,0,0, .5);
}
.hb-wrap {
  position: fixed;
  top: 0;
  right: -250px;
  width: 250px;
  height: 100vh;
  background-color: $bg-color_news;
  transition: right .3s ease-in-out;
}
body.hb_is-open .hb-wrap {
  right: 0;
}
.sp-nav-global {
  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px 0 30px;
    color: $color_main;
  }
  &__item {
    padding: 15px 0;
    font-weight: bold;
  }
}
.sp-logo_kadan {
  width: 55%;
  margin: 85px auto 0;
}
.sp-rv__btn {
  width: 100%;
  margin: 0 auto;
  background-color: $bg-color_reservation;
  font-weight: bold;
  &-inner {
    display: flex;
    justify-content: center;
    align-items: center;  
  }
  &-text {
    padding: 15px 0 15px 10px;
  }
}