$color_main: #000000;
$color_white: #ffffff;
$color_border: #707070;
$bg-color_reservation: #978F10;
$bg-color_news: #F6F6F6;

$underlayer_border: #E7E7E7;
$reservation_placeholder: #797878;

@mixin section-layer {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
}

@mixin section-layer_small {
  width: 100%;
  max-width: 1180px;
  margin: 0 auto;
}

$layout: (
  header: "1",
  page__image: "1",
  modal__bg: "2",
  modal-contents: "3",
  hb__btn: "10",
);
@mixin z-index($key) {
  z-index: #{map-get($layout, $key)};
}

// line-height計算
$main-font: 16;
@function lineHeight($lineHeight, $fontSize: $main-font) {
  @return floor($lineHeight / $fontSize * 10) / 10;
}

// ブレイクポイント
$breakpoints: (
  xs: "only screen and (max-width: 568px)",
  sm: "only screen and (max-width: 768px)",
  sub-sm: "only screen and (max-width: 880px)",
  md: "only screen and (max-width: 1024px)",
  sub-md: "only screen and (max-width: 1180px)",
  lg: "only screen and (max-width: 1600px)",
);
@mixin mq_max($breakpoint) {
  @media #{map-get($breakpoints, $breakpoint)} {
    @content;
  }
}