.plans {
  width: 100%;
  height: 100%;
  padding: 60px 0 144px;
  background: url('../../kadan-image/kadan-main/bg02.jpg') no-repeat center center;
  background-size: cover;
  @include mq-max(md) {
    padding: 80px 0 100px;   
  }
  @include mq-max(sm) {
    padding: 80px 0;   
  }
  @include mq-max(xs) {
    padding: 60px 0;    
  }
  &-inner_small {
    @include section-layer_small;
    @include mq-max(md) {
      padding: 0 10px;
    }
  }
  &__title {
    margin-bottom: 66px;
    @include mq-max(xs) {
      margin-bottom: 55px; 
    }
    &-image {
      margin: 0 auto 20px;
    }
  }
  &-card {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @include mq-max(md) {
      justify-content: space-around; 
    }
    @include mq-max(sm) {
      flex-direction: column;
    }
  }
  &__item {
    width: 331px;
    @include mq-max(md) {
      width: 30%;  
    }
    @include mq-max(sm) {
      width: 80%;
      margin: 0 auto;
    }
    @include mq-max(xs) {
      width: 100%; 
    }
  }
  &__image {
    @include mq-max(sm) {
      width: 100%;
      margin: 0 auto; 
    }
  }
  &__body {
    margin: 0 auto 50px;
  }
  &__heading {
    margin: 32px 0 20px;
    font-size: 2rem;
    line-height: lineHeight(32, 20);
    font-weight: bold;
    @include mq-max(md) {
      font-size: 1.8rem;
    }
    @include mq-max(sm) {
      font-size: 1.6rem;
    }
  }
  &__text {
    line-height: lineHeight(32);
    font-weight: bold;
  }
}