.abouts {
  overflow: hidden;
  &-inner {
    @include section-layer;
    padding: 106px 0 78px 0;
    @include mq-max(md) {
      padding: 80px 0 100px; 
    }
    @include mq-max(sm) {
      padding: 80px 10px;
    }
    @include mq-max(xs) {
      padding: 60px 10px; 
    }
  }
  &-media {
    display: flex;
    align-items: flex-start;
    margin-bottom: 122px;
    @include mq-max(md) {
      margin-bottom: 100px;
    }
    @include mq-max(sm) {
      flex-direction: column;
      align-items: center;
      margin-bottom: 60px;
    }
    &:nth-of-type(odd) {
      & .abouts__body {
        margin-left: 7.2%;
        @include mq-max(md) {
          margin-left: auto;
        }
      }
    }
    &:nth-of-type(even) {
      flex-direction: row-reverse;
      @include mq-max(sm) {
        flex-direction: column;
        align-items: center;
      }  
      & .abouts__body {
        margin-right: 7.2%;
        padding-top: 20px;
        margin-left: 22%;
        @include mq-max(lg) {
          margin-left: 13%;
        }
        @include mq-max(md) {
          margin-right: 50px;
        }
        @include mq-max(sm) {
          padding-top: 50px;
          margin: 0;
        }
        @include mq-max(xs) {
          padding-top: 30px;
        }
      }
      & .abouts__text {
        margin-bottom: 40px;
        @include mq-max(xs) {
          margin-bottom: 50px;
        }
      }
    }
    &:last-of-type {
      margin-bottom: 0;
    }
  }
  &__image {
    max-width: 55%;
    @include mq-max(md) {
      width: 50%;
      max-width: 100%;
    }
    @include mq-max(sm) {
      width: 100%; 
    }
  }
  &__body {
    width: 45%;
    margin-top: 30px;
    font-weight: bold;
    @include mq-max(lg) {
      margin-right: 10px;
    }
    @include mq-max(md) {
      width: 45%;
      margin: 0 auto; 
    }
    @include mq-max(sm) {
      width: 100%;
      padding: 50px 100px 0;
    }
    @include mq-max(xs) {
      padding: 30px 50px 0;
    }
  }
  &__title {
    margin-bottom: 50px;
    font-size: 3.6rem;
    line-height: lineHeight(62, 36);
    @include mq-max(md) {
      font-size: 2.8rem; 
    }
    @include mq-max(sm) {
      font-size: 2rem;
    }
    @include mq-max(xs) {
      margin-bottom: 30px;
    }
  }
  &__text {
    margin-bottom: 75px;
    line-height: lineHeight(32);
    @include mq-max(xs) {
      margin-bottom: 50px; 
    }
  }
  &__btn {
    width: 200px;
    height: 56px;
    border: 1px solid $color_border;
    text-align: center;
    line-height: 54px;
    cursor: pointer;
    @include mq-max(sm) {
      margin: 0 auto;
    }
  }
}