#page-onsen {
  & .page {
    &-mv {
      &__bg {
        background: url('../../../kadan-image/kadan-onsen/onsen-header.png') no-repeat center center;
      }
    }
  }
}
.onsen_hp_mt {
  @include mq-max(md) {
    margin-top: 20px;
  }
  @include mq-max(sm) {
    margin-top: 0;
  }
}
.onsen-efficacy {
  padding: 94px 0 74px;
  border-top: 1px solid $underlayer_border;
  border-bottom: 1px solid $underlayer_border;
  margin-top: 356px;
  margin-bottom: 96px;
  @include mq-max(md) {
    padding: 50px 0;
    margin: 276px 0 100px 0;
  }
  @include mq-max(sub-sm) {
    margin: 70px 0;
  }
  &-inner {
    width: 100%;
    max-width: 880px;
    margin: 0 auto;
    @include mq-max(md) {
      padding: 9.5px 50px;
    }
    @include mq-max(sm) {
      padding: 9.5px 30px;
    }
    @include mq-max(xs) {
      padding: 9.5px 10px;
    }
  }
}
.efficacy {
  &__title {
    margin-bottom: 51px;
    text-align: center;
    font-size: 3.2rem;
    line-height: lineHeight(32, 26);
    font-weight: bold;
    @include mq-max(md) {
      font-size: 2.8rem;
    }
    @include mq-max(sm) {
      font-size: 2.4rem;
    }
    @include mq-max(xs) {
      font-size: 2.2rem;
    }
  }
  &__item {
    display: flex;
    justify-content: space-between;
    padding: 9.5px 0;
    border-bottom: 1px solid $underlayer_border;
    font-size: 1.5rem;
    line-height: lineHeight(32, 15);
    font-weight: bold;
    @include mq-max(md) {
      padding: 9.5px 50px;
      font-size: 1.4rem;
    }
    @include mq-max(sm) {
      padding: 9.5px 30px;
    }
    @include mq-max(xs) {
      padding: 9.5px 10px;
    }
    &:first-of-type {
      border-top: 1px solid $underlayer_border;
    }
  }
  &__heading {
    width: 30%;
    @include mq-max(xs) {
      width: 40%;
    }
  }
  &__detail {
    width: 100%;
    max-width: 660px;
    text-align: left;
  }
}