@keyframes zoom {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.1);
  }
}
.mv {
  position: relative;
  width: 100%;
  height: 1079px;
  overflow: hidden;
  @include mq-max(md) {
    height: auto;
    padding-top: 56%;
  }
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    animation: zoom 8s 0s forwards;
    transform-origin: center center;
  }
  &__text {
    position: absolute;
    top: 360px;
    right: 26%;
    font-size: 3.4rem;
    line-height: lineHeight(42, 34);
    font-weight: bold;
    color: $color-white;
    writing-mode: vertical-rl;
    @include mq-max(md) {
      top: 140px;
      right: 15%;
      font-size: 2.8rem;
    }
    @include mq-max(sm) {
      max-width: 100%;
      top: 150px;
      right: 0;
      left: 15%;
      font-size: 2rem; 
      writing-mode: horizontal-tb;
    }
    @include mq-max(xs) {
      top: 115px;
      font-size: 1.8rem;
    }
    &_hp_pr {
      padding-right: 50px;
      @include mq-max(md) {
        padding-right: 15%;
      }
      @include mq-max(sm) {
        padding-right: 0;
        padding-top: 20px;
      }
      @include mq-max(xs) {
        padding-top: 10px;
      }
    }
  }
}
