.access {
  background: url('../../kadan-image/kadan-main/bg.jpg') no-repeat center center;
  background-size: cover;
  width: 100%;
  height: 100%;
  padding: 60px 0 110px;
  @include mq-max(md) {
    padding: 80px 0 100px;   
  }
  @include mq-max(sm) {
    padding: 80px 0;   
  }
  @include mq-max(xs) {
    padding: 60px 0;   
  }
  &-inner_small {
    @include section-layer_small;
    @include mq-max(md) {
      padding: 0 10px; 
    }
  }
  &__title {
    margin-bottom: 84px;
    @include mq-max(xs) {
      margin-bottom: 55px;
    }
  }
  &-media {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 69px;
    @include mq-max(sm) {
      flex-direction: column;
      align-items: center;
      margin-bottom: 0;
    }
  }
  &__image {
    @include mq-max(md) {
      width: 50%; 
    }
    @include mq-max(sm) {
      width: 100%;
    }
  }
  &__body {
    max-width: 441px;
    margin-top: 10px;
    margin-left: 30px;
    font-weight: bold;
    @include mq-max(sm) {
      width: 100%;
      max-width: 100%;
      padding-left: 60px;
      margin: 60px 0; 
    }
    @include mq-max(xs) {
      padding-left: 30px;
      margin: 30px 0; 
    }
  }
  &__heading,
  &__text, 
  .access_hp_mb {
    // line-height: lineHeight(32);
    margin-bottom: 24px;
    @include mq-max(md) {
      margin-bottom: 15px;
    }
  }
  &_announce {
    line-height: 1.5;
  }
}
.access_hp_pb {
  padding-bottom: 16px;
}
.iframe-wrap {
  position: relative;
  width: 100%;
  max-width: 792px;
  padding-top: 50%; /* =height590px 2px少ない */
  margin: 0 auto;
  @include mq-max(md) {
    max-width: 100%;
  }
  @include mq-max(sm) {
    padding-top: 70%; 
  }
}
.iframe-wrap iframe {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}