.modal {
  &__bg {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0, .5);
    @include z-index(modal__bg);
  }
  &-contents {
    display: none;
    position: fixed;
    top: 20%;
    left: 0;
    right: 0;
    width: 100%;
    max-width: 550px;
    height: auto;
    padding: 30px 60px 90px;
    margin: 0 auto;
    background-color: $color_white;
    @include z-index(modal-contents);
    @include mq-max(md) {
      max-width: 450px;
    }
  }
  &__btn {
    position: absolute;
    top: 20px;
    right: 22px;
    width: 18px;
    height: 18px;
    cursor: pointer;
  }
  &__title {
    text-align: center;
    font-size: 2rem;
    line-height: lineHeight(34, 20);
    font-weight: bold;
    &::after {
      content: '';
      display: block;
      width: 100%;
      margin-top: 30px;
      margin-bottom: 35px;
      border-bottom: 1px solid $underlayer_border;
    }
    @include mq-max(md) {
      font-size: 1.8rem;
    }
  }
}
.form {
  &_alert {
    font-size: 1.4rem;
    font-weight: bold;
    line-height: lineHeight(24,14);
  }
  &_hp_lh {
    line-height: lineHeight(24);
  }
}
label {
  margin: 14px 0;
  font-weight: bold;
  line-height: lineHeight(27);
}
label,
input[type=text],
select,
input[type=submit] {
  display: block;
}
input[type=text],
select {
  width: 100%;
  max-width: 429px;
  border: 1px solid $underlayer_border;
  font-size: 1.4rem;
  color: $reservation_placeholder;
}
input[type=text] {
  padding: 10px;
}
select {
  padding: 10px;
}
input[type=submit] {
  width: 198px;
  padding: 13px 0;
  margin: 64px auto 0;
  border: 1px solid $color_border;
  text-align: center;
  line-height: lineHeight(27);
}
