.history {
  width: 100%;
  height: 681px;
  background: url('../..//kadan-image/kadan-main/bg.jpg') no-repeat center center;
  background-size: cover;
  font-weight: bold;
  @include mq-max(sm) {
    height: auto;     
  }
  &-inner {
    display: flex;
    flex-direction: row-reverse;
    @include section-layer-small;
    padding-top: 64px;
    padding-right: 5.3%;
    @include mq-max(sub-md) {
      padding-right: 15%;
    }
    @include mq-max(sm) {
      flex-direction: column;
      padding: 50px 10px 15px;
    }
  }
  &__heading {
    font-size: 2.8rem;
    line-height: lineHeight(42, 28);
    @include mq-max(md) {
      font-size: 2.4rem;
    }
    @include mq-max(sm) {
      font-size: 2rem;
    }
    @include mq-max(xs) {
      font-size: 1.8rem;
    }
    &_hp_pt {
      padding-top: 92px;
      @include mq-max(sm) {
        padding-top: 10px;
      }
    }
  }
  &__text {
    padding-left: 11%;
    letter-spacing: -5px;
    writing-mode: vertical-rl;
    text-orientation: upright;
    @include mq-max(sub-md) {
      padding-left: 8%;
    }
    @include mq-max(sm) {
      padding-left: 0;
      margin-bottom: 35px;
      text-align: center;
      line-height: 1.75;
      writing-mode: horizontal-tb;
      text-orientation: unset;
      letter-spacing: 1px;
    }
    @include mq-max(xs) {
      margin-bottom: 20px; 
      line-height: 1.5;
    }
    & > :not(:last-child) {
      padding-left: 20px;
      @include mq-max(sm) {
        padding-left: 0;
      }
    }
    &:last-child {
      padding-left: 0;
    }
  }
  &_hp_pt {
    padding-top: 95px;
    @include mq-max(sm) {
      padding-top: 0;
    }
  }
  &__name {
    font-size: 2.2rem;
    line-height: lineHeight(32, 22);
    text-align: end;
    margin-bottom: 20px;
    @include mq-max(md) {
      font-size: 2rem;
    }
    @include mq-max(sm) {
      font-size: 1.8rem;
    }
    @include mq-max(xs) {
      font-size: 1.6rem; 
    }
  }
}