.footer {
  width: 100%;
  padding-top: 65px;
  background: url('../../kadan-image/kadan-main/bg3.jpg') no-repeat center center;
  background-size: cover;
  @include mq-max(sm) {
    padding-top: 50px;   
  }
  @include mq-max(xs) {
    padding-top: 30px;   
  }
  &-inner {
    @include section-layer;
  }
  &__title {
    margin: 0 auto 100px;
    writing-mode: vertical-rl;
    font-size: 1.8rem;
    line-height: lineHeight(32, 18);
    font-weight: bold;
    @include mq-max(md) {
      font-size: 1.6rem;
      margin: 0 auto 50px; 
    }
    @include mq-max(sm) {
      margin: 0 auto 50px; 
    }
    @include mq-max(xs) {
      margin: 0 auto 30px; 
      font-size: 1.4rem;
    }
    &-text  {
      padding: 0 12px;
    }
  }
  &__image {
    margin: 0 auto 57px;
    @include mq-max(md) {
      margin: 0 auto 50px; 
    }
    @include mq-max(sm) {
      width: 18%;
      margin: 0 auto 30px; 
    }
    @include mq-max(xs) {
      width: 23%; 
    }
  }
  &__body {
    margin-bottom: 32px;
    text-align: center;
    line-height: lineHeight(48, 16);
    font-weight: bold;
  }
  &-wrap {
    display: flex;
    justify-content: center;
  }
  &_tel,
  &_fax {
    padding: 0 10px;
  }
  &_hp_pr {
    padding-right: 21px;
  }
  &_hp_spacing {
    letter-spacing: 2.88px;
  }
}
.copyright {
  padding: 24px 0;
  background-color: $color_main;
  text-align: center;
  font-size: 1.2rem;
  line-height: lineHeight(24, 12);
  color: $color_white;
  @include mq-max(md) {
    padding: 18px 0;   
  }
  @include mq-max(sm) {
    padding: 15px 0;
  }
}