html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  background-color: $color_white;
  color: $color_main;
  font-family: “Times New Roman”, “游明朝”, “Yu Mincho”, “游明朝体”, “YuMincho”, “ヒラギノ明朝 Pro W3”, “Hiragino Mincho Pro”, “HiraMinProN-W3”, “HGS明朝E”, “ＭＳ Ｐ明朝”, “MS PMincho”, serif;
  @include mq-max(lg) {
    font-size: 1.6rem;
  }
  @include mq-max(sub-md) {
    font-size: 1.4rem;
  }
  @include mq-max(md) {
    font-size: 1.4rem;
  }
  @include mq-max(sub-sm) {
    font-size: 1.4rem;
  }
  @include mq-max(sm) {
    font-size: 1.4rem;
  }
  @include mq-max(xs) {
    font-size: 1.4rem;
  }
}

a {
  display: block;
  cursor: pointer;
}

img {
  display: block;
  max-width: 100%;
}

span {
  display: block;
}

button {
  display: block;
}

.section__title-image {
  margin: 0 auto 20px;
}

.section__title-text {
  text-align: center;
  font-size: 3.6rem;
  font-weight: bold;
  @include mq-max(md) {
    font-size: 2.8rem;    
  }
  @include mq-max(sm) {
    font-size: 2rem;
  }
}