.news {
  width: 100%;
  padding: 60px 0;
  background-color: $color_white;
  @include mq-max(md) {
    padding: 80px 0 100px;   
  }
  @include mq-max(sm) {
    padding: 80px 0;
  }
  @include mq-max(xs) {
    padding: 60px 0;   
  }
  &-inner_small {
    @include section-layer_small;
    padding: 0 10px;
    @include mq-max(md) {
      padding: 0 10px; 
    }
  }
  &__title {
    margin-bottom: 46px;
  }
}
.tab-list {
  display: flex;
  justify-content: center;
  margin-bottom: 49px;
  font-size: 0;
}
.tab {
  margin: 0 26.5px;
  font-size: 1.6rem;
  font-weight: bold;
  @include mq-max(md) {
    font-size: 1.4rem;   
  }
}
.contents {
  display: flex;
  justify-content: space-between;
  flex-flow: row wrap;
  @include mq-max(sm) {
    flex-flow: column nowrap;   
  }
  &__body {
    padding-left: 30px;
    font-weight: bold;
    @include mq-max(xs) {
      padding-left: 15px; 
    }
    & > time {
      display: block;
      margin-bottom: 10px;
    }
  }
}
.contents-media {
  display: flex;
  width: 48.31%;
  padding: 15px 10px;
  margin-bottom: 20px;
  background-color: $bg-color_news;
  @include mq-max(sm) {
    width: 100%;
  }
  &:last-of-type,
  &:nth-last-of-type(2) {
    margin-bottom: 0;
  }
  &:nth-last-of-type(2) {
    @include mq-max(sm) {
      margin-bottom: 20px;
    }
  }
}
.current {
  text-decoration: underline $color_main;
}
