.page {
  & .header {
    position: relative;
  }
  & .nav-global {
    color: $color_main;
  }

  &-mv {
    position: relative;
    @include section-layer;
    height: 500px;
    @include mq-max(sm) {
      height: auto;
      padding-top: 45%;
    }
    &__bg {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      background-size: cover;
      width: 100%;
      height: 100%;
      margin: 0 auto;
    }
    &__text {
      position: absolute;
      top: 50%;
      right: 0;
      left: 0;
      margin: 0 auto;
      transform: translateY(-50%);
      text-align: center;
      font-size: 3.4rem;
      line-height: lineHeight(42, 34);
      font-weight: bold;
      color: $color_white;
      @include mq-max(md) {
        font-size: 2.8rem;
      }
      @include mq-max(sm) {
        font-size: 2.4rem;
      }
      @include mq-max(xs) {
        font-size: 2.2rem;
      }
    }
  }
  
  &-history {
    margin-bottom: 78px;
    @include section-layer_small;
    &__text {
      text-align: center;
      line-height: lineHeight(32);
      font-weight: bold;
    }
  }

  &-main {
    &-inner {
      @include section-layer_small;
      @include mq-max(sm) {
        padding: 0 10px;
      }
    }
  }
  &-media {
    position: relative;
    display: flex;
    align-items: flex-start;
    margin-bottom: 240px;
    @include mq-max(md) {
      margin-bottom: 260px;
    }
    @include mq-max(sub-sm) {
      margin-bottom: 280px;
    }
    @include mq-max(sm) {
      flex-direction: column;
      margin-bottom: 50px;
    }
    &:nth-of-type(odd) {
      & .page__body {
        right: 0;
        padding-left: 20%;
        @include mq-max(sub-md) {
          padding-left: 22%;
        }
        @include mq-max(md) {
          padding-left: 19%;
        }
        @include mq-max(sub-sm) {
          padding-left: 15%;
        }
        @include mq-max(sm) {
          height: auto;
          padding: 60px 50px;
        }
        @include mq-max(xs) {
          padding: 60px 50px 40px;
        }
      }
    }
    &:nth-of-type(even) {
      flex-direction: row-reverse;
      @include mq-max(sm) {
        flex-direction: column;
      }
      & .page__body {
        left: 0;
        padding-left: 5%;
        @include mq-max(sm) {
          height: auto;
          padding: 60px 50px;
        }
        @include mq-max(xs) {
          padding: 60px 50px 40px;
        }
      }
    }
  }
  &__image {
    @include z-index(page__image);
    @include mq-max(sub-md) {
      width: 45%;
    }
    @include mq-max(md) {
      width: 42%;
    }
    @include mq-max(sub-sm) {
      width: 40%;
    }
    @include mq-max(sm) {
      width: 100%;
    }
  }
  &__body {
    position: absolute;
    top: 57px;
    width: 100%;
    max-width: 813px;
    height: 381px;
    padding: 60px 0 110px;
    background: url('../kadan-image/kadan-main/bg.png') no-repeat center center;
    background-size: cover;
    font-weight: bold;
    @include mq-max(sub-md) {
      max-width: 750px;
    }
    @include mq-max(md) {
      max-width: 650px;
      height: 355px;
    }
    @include mq-max(sub-sm) {
      max-width: 550px;
    }
    @include mq-max(sm) {
      position: relative;
      top: 0;
      max-width: 100%;
    }
  }
  &__heading {
    margin-bottom: 52px;
    font-size: 2.4rem;
    line-height: lineHeight(32, 24);
    @include mq-max(md) {
      margin-bottom: 35px;
      font-size: 2.2rem;
    }
    @include mq-max(sm) {
      font-size: 2rem;
    }
  }
  &__text {
    margin-bottom: 52px;
    line-height: lineHeight(32);
    @include mq-max(md) {
      margin-bottom: 35px;
    }
  }
  &__detail {
    font-size: 1.4rem;
    line-height: lineHeight(21, 14);
  }
}

.breadcrumb {
  margin-top: 36px;
  margin-bottom: 98px;
  @include section-layer_small;
  @include mq-max(md) {
    margin-bottom: 75px;
  }
  @include mq-max(sm) {
    margin-bottom: 55px;
  }
  &__list {
    display: flex;
    align-items: center;
    font-weight: bold;
    @include mq-max(md) {
      margin-left: 30px;
    }
  }
  &__item {
    padding: 0 5px;
    line-height: lineHeight(32);
  }
}